//
// sidebar
// --------------------------------------------------
// 模块：侧边工具栏
// 类名：sidebar
// --------------------------------------------------
// bootstrap 默认Less
@import "../bootstrap/variables.less";
// 思诺博UI Less
@import "../sinoui-variables.less";    // 变量

.sidebar,
.sidebar-wrap {
    height: 100%;
    right: 0;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 9999;
    height: 100%;
    background-color: @white;
    -webkit-transition: width linear .4s;
    -moz-transition: width linear .4s;
    -ms-transition: width linear .4s;
    transition: width linear .4s;
}

.sidebar-box-wraper.sidebar-box-dockright {
    margin-left: 0;
    left: auto;
    right: 4px;
}

.sidebar-panel {
    width: 40px;
    height: 100%;
    position: absolute;
    background: #444851;
    z-index: 2;
    top: 0;
    right: 0;
    a {
        display: block;
        line-height: 200px;
        width: 40px;
        height: 38px;
        text-decoration: none;
        color: #d8d8d8;
        overflow: hidden;
        background-color: #444851;
        position: relative;
        overflow: hidden;
        top: 0;
        left: 0;
        z-index: 1;
        margin: 0;
        padding: 0;
    }
    .bar-tips {
        height: 38px;
        line-height: 38px;
        width: 92px;
        position: absolute;
        z-index: 2;
        left: -121px;
        top: 0;
        background: #444851;
        color: #d8d8d8;
        text-align: center;
        display: block;
        visibility: hidden;
    }
}

.sidebar-box {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -190px;
    background: #444851;
    z-index: 2;
    width: 40px;
    li {
        position: relative;
        display: block;
        left: 0;
        top: 0px;
        a {
            text-align: center;
            &:hover,
            &.current {
                background: @red;
                text-decoration: none;
            }
            i {
                display: inline-block;
                cursor: pointer;
                vertical-align: top;
                position: absolute;
                width: 20px;
                height: 20px;
                position: relative;
                margin-top: 10px;
                font-size: 1.4em;
            }
        }
        .bar-tips i {
            height: 10px;
            width: 10px;
            top: 8px;
            right: -7px;
            color: #444851;
            position: absolute;
            margin-top: 0;
        }
        .return-top {
            display: none;
        }
        &.li-gotop .return-top {
            display: block;
        }
        &.li-shopcart {
            a {
                line-height: 16px;
                height: 140px;
                color: @white;
                margin-bottom: 1px;
                padding-top: 8px;
            }
            .bar-name {
                width: 16px;
                display: block;
                height: 48px;
                margin: 15px 0 0 14px;
                cursor: pointer;
            }
            .bar-num {
                width: 21px;
                height: 21px;
                display: block;
                border-radius: 50%;
                background: @red;
                text-align: center;
                line-height: 21px;
                margin: 9px 0 0 10px;
                cursor: pointer;
                color: @white;
            }
        }
    }
}

.sidebar-box-min .sidebar-panel {
    right: 280px;
}

.sidebar-box-pop {
    position: absolute;
    top: 0;
    right: 0;
    width: 280px;
    display: none;
    background-color: @white;
    box-shadow: 0 0 5px #999;
    border-radius: 5px 0 0 5px;
    border: 1px solid #999;
    border-right: 0;
    .bar-panel {
        width: 100%;
        position: relative;
    }
    .no-data {
        color: #333;
        font-size: 14px;
        padding: 35px 10px;
        text-align: center;
        i {
            display: inline-block;
            margin: 0 10px 0 0;
            height: 36px;
            width: 60px;
            vertical-align: middle;
        }
    }
    .sidebar-shopcart{
        .shopcart-list{
            overflow-y: scroll;
            padding: 0 10px;
        }
        .shopcart-info{
            span{
                line-height: 1.5;
                padding: 5px;
                display: inline-block;
            }
        }
    }
}

.bar-title {
    color: #333;
    margin-left: 10px;
    h3 {
        margin: 0;
        line-height: 40px;
        font-size: 14px;
        font-weight: 700;
    }
}

.bar-close {
    display: inline-block;
    cursor: pointer;
    vertical-align: top;
    position: absolute;
    width: 16px;
    height: 16px;
    overflow: hidden;
    position: absolute;
    top: 12px;
    right: 4px;
    i {
        margin-top: 0;
        color: #444851;
        font-size: 1.5rem;
    }
}
.fly-img{
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    position: fixed;
    z-index: 9999;
}
